header {
    height: 100vh;
    padding-top: 3rem;
    overflow: hidden;
}

.header-container {
    text-align: center;
    height: 100%;
    position: relative;
}

h1, h5 {
    text-align: center;
    color: var(--color-white);
    font-weight: 500;
}


h1 {
    margin-top: 7.5rem;
    margin-bottom: 2rem;
    font-size: 2.5rem;
}

h5 {
    margin-top: 4rem;
    margin-bottom: 5rem;
    font-size:1.1rem;
}


/*CTA styles*/
.cta {
    margin-top: 2.5rem;
    display: flex;
    justify-content: center;
}

/*header socials styles*/
.header-socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    position: absolute;
    left: 1rem;
    bottom: 10rem;
}


/*scroll down*/
.scroll-down {
position: absolute;
right: -1.5rem;
bottom: 12rem;
-webkit-transform: rotate(90deg);
        transform: rotate(90deg);
font-weight: 300;
font-size: 1rem;
}

/* media queries */
@media (max-width: 768px) {
    header {
        height: 100vh;
        line-height: normal;
        font-size: small;
    }
    .header-socials {
        display: block;
        position: relative;
        bottom:0px;
        margin-top: 2rem;
        margin-right: 1.3rem;
        grid-gap: 0.5rem;
        gap: 0.5rem;
        
    }


    .scroll-down {
        display: none;
    }

  

}


nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0, 0, 0, 0.4);
}

nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}

/* media queries */
@media (max-width: 768px) {
    nav {
        display: none;

    }
}
.about-container {
    display:grid;
    grid-template-columns: 35% 50%;
    grid-gap:15%;
    gap:15%;

}

h2 { 
    text-align: center;
    margin-bottom: 5rem;
    font-size: 2rem;
    font-weight: 300;

}

.about-me {
    width: 100%;
    aspect-ratio: 1/1;
    display: grid;
    place-items: center;
}
.me-image {
 background: linear-gradient(var(--color-primary),transparent);
 display: grid;
 place-items: center;
    width: 20rem;
    height: 30rem;
    position: absolute;
    left: calc(10%);
    border-radius: 12rem 12rem 0 0;
    margin-top: 4rem;
    overflow: hidden;
    padding: 1.5rem 1.5rem 1.5rem 1.5rem;  
}

.skills-cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-right: 8rem;
}
.skills-card {
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 12rem 12rem 0 0;
    padding: 2rem 1rem;
    text-align:center;
}

.skills-card:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}
.about-icon {
    color: var(--color-primary);
    font-size: 1.7rem;
    margin-bottom: .8rem;
}

.skills-card  h5 {
    font-size:  0.9rem;
    color: var(--color-white);
}

.skills-card small {
    font-size: .9rem;
    color: var(--color-light);
}

.about-content p {
    margin: 2rem 0 2.6rem;
    font-size: 1rem;
    margin-right: 8rem;
}

/* media queries */
@media (max-width: 768px) {



    .me-image {
       display: none;
        
}

    

    .skills-cards {
        display: flex;
        justify-content: center;
        margin-left: -4rem;
        grid-gap: 0.2rem;
        gap: 0.2rem;
   

    }

  
    .about-content p{
        display: none;
    }

}
#portfolio {
    height: auto;
}

h2 {
    margin-top: 4rem;
}
.portfolio-container {
    width: 80%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
   margin-left: 8rem;
}
.portfolio-item{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);


}

.portfolio-item:hover {

    border-color: var(--color-primary-variant);
    background: transparent;
}

.portfolio-item-image {
    border-radius: 1.5rem;
    overflow:hidden;
    background: var(--color-bg-variant);
    opacity: 0.7;

}


.portfolio-item h3 {
    margin: 1.2rem 0 2rem;
}

.portfolio-item-cta {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* media queries */
@media (max-width: 768px){
    .portfolio-container {
        grid-template-columns: 1fr;
        margin-left: 2.5rem;
    }
}
.contact-container {
    width: 80%;
    display: grid;
    grid-template-columns: 55% 40%;
    grid-gap: 10%;
    gap: 10%;
}

.contact-options {
    display:flex;
    flex-direction:column;
    margin-left: 15rem;
}

.contact-option {
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    margin-top: .7rem;
    display: inline-block;
    font-size: .9rem;
}

.contact-option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact-option-icon {
    font-size: 1.6rem;
    margin-bottom: .5rem;
}


form {
    margin-top: .7rem;
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: .5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
    font: var(--font-family-text);

}



/* media queries */

@media (max-width: 768px) {
    .contact-container {
        width: var(--container-width-sm);
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;
        margin-left: 1rem;
    }
    

    .contact-options {
    display:flex;
    flex-direction:column;
   margin: auto;      
    }

    .btn {
        margin: auto;
    }
}
.footer {
    font-size: .9rem;
}


* { 
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;

}

:root {
    --color-bg: #222330;
    --color-bg-variant: #37374c;
    --color-primary: #8166aa;
    --color-primary-variant: #a191b9;
    --color-white: #fff;
    --color-lightgrey: #bdc4d0;
    --color-light: #fff(255. 255, 255. 0.6);
    --color-black: #101010;

    --container-width-lg:   75%;
    --container-width-md:   86%;
   --container-width-sm:   90%;
   --font-family-text: "Poppins", sans-serif;

}

html {
    scroll-behavior: smooth;
  }

  body {
    background: #222330;
    background: var(--color-bg);
    color: #fff;
    color: var(--color-white);
    font-family: "Poppins", sans-serif;
    font-family: var(--font-family-text);
    line-height: 1.7em;
  }
  
/* general styles */

.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
  
  }
  
   h2, h3, h4 {
    font-weight: 500;
  }
  
  
  img {
    display: block;
    width: 100%;
    object-fit: cover;
  }
  
  .text-light {
    color: #fff;
    color: var(--color-white);
  
  }
  
  a {
    color: #8166aa;
    color: var(--color-primary);
    transition: var(--transition)
  }
  
  a:hover {
    color: #fff;
    color: var(--color-white)
  }
  
  
  .btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    padding: 0.75rem 1.2rem;
    border-radius: 15px;
    cursor: pointer;
    border: 1px solid #222330;
    border: 1px solid var(--color-bg);
    color: #fff;
    color: var(--color-white);
    background-color: #8166aa;
    background-color: var(--color-primary);
    font: "Poppins", sans-serif;
    font: var(--font-family-text);
  }

  .btn-primary {
      width: -webkit-max-content;
      width: max-content;
      display: inline-block;
      padding: 0.75rem 1.2rem;
      border-radius: 15px;
      cursor: pointer;
      border: 1px solid #222330;
      border: 1px solid var(--color-bg);
      color: #fff;
      color: var(--color-white);
      background-color: #a191b9;
      background-color: var(--color-primary-variant);
      font: "Poppins", sans-serif;
      font: var(--font-family-text);
    
  }
  
  .btn:hover {
    background: #a191b9;
    background: var(--color-primary-variant);
    color: #222330;
    color: var(--color-bg);
    border-color: transparent;
  }

  .btn-primary:hover {
    background: #8166aa;
    background: var(--color-primary);
    color: #222330;
    color: var(--color-bg);
    border-color: transparent;
  }
  

  

  
  /* media queries */
  @media (max-width: 768px){
    .container {
      width: 90%;
      width: var(--container-width-sm);
    }
  }
  

